import { WebPlugin } from '@capacitor/core';

import type { UnzipOptions, ZipOptions, ZipPlugin } from './definitions';

export class ZipWeb extends WebPlugin implements ZipPlugin {
  async zip(_options: ZipOptions): Promise<void> {
    this.throwUnimplementedError();
  }

  async unzip(_options: UnzipOptions): Promise<void> {
    this.throwUnimplementedError();
  }

  private throwUnimplementedError(): never {
    throw new Error('Not implemented on web.');
  }
}
